import $app from './modules/$app.js'
import $user from './modules/$user.js'

const modules = {
  $app,
  $user
};
// APPmodules
let notSaveStateKeys = ['$error'];

let lifeData = uni.getStorageSync('lifeData') || {};

// const modulesFiles = require.context('./modules', true, /\.js$/);

